              </template>
              <el-menu-item index="4-1">
                <i class="el-icon-menu"></i>
                <span slot="title">订单列表</span>
              </el-menu-item>
            </el-submenu>
            <el-submenu index="5">
              <template slot="title">
                <i class="el-icon-data-line"></i>
                <span slot="title">数据统计</span>
              </template>
              <el-menu-item index="5-1">
                <i class="el-icon-menu"></i>
                <span slot="title">数据报表</span>
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>-->

        <el-aside width="200">
          <div class="wsy_top_bar" @click="isCollapse = !isCollapse">|||</div>
          <el-menu
            router
            :default-active="defaultActive"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            :collapse="isCollapse"
            background-color="#333744"
            text-color="#fff"
            active-text-color="#409EFF"
          >
            <el-submenu v-for="(item) in menusList" :key="item.path" :index="item.path">
              <template slot="title">
                <!-- <span v-show='ind == index' v-for='(item,index) in iconArr' :key='index'  :class="item"></span> -->
                <i class="el-icon-user"></i>
                <span slot="title">{{ item.authName }}</span>
              </template>
              <el-menu-item v-for="(it) in item.children" :key="it.path" :index="it.path">
                <i class="el-icon-menu"></i>
                <span slot="title" @click="ToPath(item,it)">{{ it.authName }}</span>
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <!-- 顶部面包屑导航 -->
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home/index' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item v-if="!breadCrumbStrA == ''">{{ breadCrumbStrA }}</el-breadcrumb-item>
            <el-breadcrumb-item v-if="!breadCrumbStrB == ''">{{ breadCrumbStrB }}</el-breadcrumb-item>
          </el-breadcrumb>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "home",
  components: {},
  props: {},
  data() {
    return {
      iconArr:[
        "iconfont icon-tubiao-01",
        "iconfont icon-tubiao-04",
        "iconfont icon-tubiao-19",
        "iconfont icon-tubiao-02",
        "iconfont icon-tubiao-02",
      ],
      isCollapse: true,
      menusList: [],
      breadCrumbStrA: "",
      breadCrumbStrB: "",
      defaultActive: ""
    };
  },
  created() {},
  mounted() {
    this.getToken();
    this.getMenus();
    let defaultRouter = JSON.parse(
      window.sessionStorage.getItem("defaultRouter")
    );
    this.breadCrumbStrA = JSON.parse(window.sessionStorage.getItem("str")) || "";
    this.breadCrumbStrB = JSON.parse(window.sessionStorage.getItem("str1")) || "";
    this.defaultActive = defaultRouter || "";
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    // 获取Token
    getToken() {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$router.push("/");
      }
    },
    getMenus() {
      this.$axios.get("/menus").then(({ data }) => {
        // console.log(data);
        this.menusList = data.data;
      });
    },
    handleOpen(key, keyPath) {
      //   console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      //   console.log(key, keyPath);
    },
    // 跳转至用户列表
    toUsers() {
      this.$router.push("/home/user");
    },
    // 退出登陆
    quit() {
      this.$confirm("确定要退出吗, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          window.sessionStorage.removeItem("token");
          this.getToken();
          this.$message({
            type: "success",
            message: "退出成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消"
          });
        });

      // this.$router.push("/");
    },
    ToPath(item, it) {
      // console.log(item, it);
      this.breadCrumbStrA = item.authName;
      this.breadCrumbStrB = it.authName;
      window.sessionStorage.setItem("str", JSON.stringify(item.authName));
      window.sessionStorage.setItem("str1", JSON.stringify(it.authName));
      // this.$router.push(it.path)
    }
  },
  filters: {},
  computed: {},
  watch: {
    $route: {
      handler(val) {
        window.sessionStorage.setItem(
          "defaultRouter",
          JSON.stringify(String(val.path).slice(6))
        );
      },
      deep: true
    }
  }
};
</script>

<style lang='scss' scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.wsy_main {
  width: 100%;
  height: 100%;
  > .el-container {
    width: 100%;
    height: 100%;

    .el-header {
      background-color: #373d41;
      padding-left: 20px;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .wsy_header_left {
        display: flex;
        align-items: center;
        > img {
          width: 56px;
          height: 56px;
          border-radius: 50%;
          margin-right: 15px;
        }
        > .title {
          font-size: 22px;
          color: #fff;
        }
      }
    }
    .el-aside {
      // max-width: 200px;
      // flex-shrink: 0;
      background-color: #333744;
      .wsy_top_bar {
        background-color: #4a5064;
        color: #fff;
        font-size: 12px;
        text-align: center;
        line-height: 22px;
        cursor: pointer;
        letter-spacing: 0.12em;
      }
      .el-menu {
        border-right: 0;
      }
    }
    .el-main {
      background-color: #eaedf1;
      display: block;
      -webkit-box-flex: 1;
      flex: 1;
      flex-basis: auto;
      overflow: auto;
      box-sizing: border-box;
      padding: 20px;
    }
  }
}
</style>
